body {
  background: #0e1414;
  color: #fff;
  font-family: "Open Sans";
}

.answerYourBody {
  margin-bottom: 180px;
}
.answerYourBody img {
  width: 100%;
  height: auto;
}
.answerYourBody h2, .answerYourBody .h2 {
  font-weight: 100;
  border-bottom: 1px #fff solid;
  font-size: 26px;
  letter-spacing: 0.5px;
  color: rgba(255, 255, 255, 0.8);
}
.answerYourBody h4, .answerYourBody .h4 {
  font-size: 16px;
}
.answerYourBody .text-red {
  color: #cf1430;
}
.answerYourBody .topParagraph p {
  line-height: 30px;
  font-size: 18px;
  font-weight: 100;
  opacity: 0.8;
}
.answerYourBody .bottomParagraph p {
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 1.7;
}

.allTheLatest h1, .allTheLatest .h1 {
  font-size: 42px;
  opacity: 0.9;
}
.allTheLatest .imgContainer img {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border-bottom: #cf1430 5px solid;
  width: 100%;
  height: auto;
}
.allTheLatest .imgDesc {
  margin-top: 30px;
}
.allTheLatest .imgDesc .title {
  font-size: 18px;
  color: #fff;
  margin-bottom: 5px;
}
.allTheLatest .imgDesc .desc {
  line-height: 30px;
  font-size: 16px;
  font-weight: 100;
  opacity: 0.5 !important;
}
.allTheLatest .button a {
  color: #fff;
  text-decoration: none;
  border-bottom: 1px #cf1430 solid;
  letter-spacing: 1.2px;
  font-size: 14px;
  font-weight: 500;
  padding-bottom: 8px;
}

.modal-body {
  background-color: #0e1414;
  border-radius: 5px;
}

.close {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 999;
  font-size: 30px;
  font-weight: normal;
  color: #cf1430;
  background-color: #0e1414;
  border: none;
  border-radius: 5px;
  width: 28px;
  height: 28px;
  opacity: 1;
  line-height: 0;
}

